                    <p style="margin: 4px 0 4px 0" class="cardContent"><b>Arranque:</b> ${marker.arranque} </p>
                    <p style="margin: 4px 0 4px 0" class="cardContent"><b>Operacion:</b> ${marker.operacion} </p>
                    <p style="margin: 4px 0 4px 0" class="cardContent"><b>Fecha:</b> ${marker.fecha} </p>
                  </div>
              </div>`;
    },

    setIntervalTimer() {
      this.intevalAdvance = setInterval(() => {
        this.obtainDataGps();
      }, 9000);
    },

    async obtainFirstLocation() {

      let firstLocation = JSON.parse(await api.obtainFirstLocation());

      try {
        this.center = firstLocation[0];
      } catch (error) {
        console.error(error);
      }
    },

    async obtainDataGps() {

      let dataCoordinates = JSON.parse(await api.obtainCoordinatesTrains(this.intevalAdvance));
      this.pathsProduccion = [];

      try {
        this.pathArranque = dataCoordinates[0];

        for (let data of dataCoordinates[2]) {

          this.pathsProduccion.push(data);
        }

        this.markers = dataCoordinates[1];
      } catch (error) {
        console.error(error);
      }
    },
  },
  async mounted() {

    let login = localStorage.getItem("login_token");
    if (!login) {
      this.$router.push("/");
    }

    try {
      document.getElementsByClassName("ifLogin")[0].className = "noLogin";
    } catch (error) {
      console.error(error)
    }

    api.getLocomotoras();

    await this.obtainFirstLocation();
    await this.obtainDataGps();
