    notifyVue(verticalAlign, horizontalAlign, color=4, message="Se presento un error, revisar la parte de notificaciones para mas detalles") {
      // const color = Math.floor(Math.random() * 4 + 1);
      this.$notifications.notify({
        message: message,
        icon: "nc-icon nc-app",
        horizontalAlign: horizontalAlign,
        verticalAlign: verticalAlign,
        type: this.type[color]
      });
    },

    obtainDatacharts() {

      let xLegend = "";
      let groupBy = document.getElementById("groupByChart").value;
      let typeChart = document.getElementById("typeChart").value;

      switch (groupBy) {
        case "diesel":
          this.encabezado = "Consumo acumulado de diesel";
          this.encabezadoAvg = "Consumo Promediado de diesel";
          break;

        case "kilometer":
          this.encabezado = "Consumo acumulado de kilometros";
          this.encabezadoAvg = "Consumo Promediado de kilometros";
          break;
      }

      let labelsTest;
      let arrayDataLineChart = [];
      let arrayDataBarChart = [];

      // typeChart = "month";

      switch (typeChart) {

        case "day":
          this.tipoReporte = "Del Día";
          arrayDataBarChart.push( api.obtainDataChartBars() );
          labelsTest = arrayDataBarChart[0].map( data => data.hour );
          xLegend = "Horas";
          break;

        case "month":
          this.tipoReporte = "Mensual";
          arrayDataBarChart.push( api.obtainDataChartBars());
          labelsTest = api.daysInMonthTest();
          xLegend = "Dias";
          break;
      }

      //Barchar Data
      this.barChart.data.labels = labelsTest;
      this.barChart.data.datasets[0].data = arrayDataBarChart[0].map( data => data.total );

      this.barChart.options.scales.x.title.text = xLegend;
      this.barChart.key = new Date().getSeconds().toString();

      this.showCharts();
    },

    showCharts(){

      this.saveChartBar.destroy();

      const ctxBar = document.getElementById('barChart');
      this.saveChartBar = new Chart(ctxBar, this.barChart);
    },

    showNotifications() {
      this.notifyVue("top", "right");
    },

    getDataNotf(){
      let dataObtained = api.obtenerNotificaciones();
      this.table1.data = [...dataObtained];
    }
  },
  mounted() {
    let login = localStorage.getItem("login_token");
    if (!login) {
      this.$router.push("/");
    }

    try {
      document.getElementsByClassName("ifLogin")[0].className = "noLogin";
    } catch (error) {
      console.error( error )
    }

    api.setActualHour();

