const $ = require('jquery');

// const urlApi = "http://ferromex.api:8080/api/index.php/authuser";
const urlApi = "/back/index.php/authuser";
// const urlApi = "/authuser";

// let isAuthorized = [];
let dataToReturn;

export default {

  loginUser(user, password) {

    let isAuthorized = obtainTokenLogin(user, password);

    return isAuthorized;
  },

  // setUrlForm(){

  //   document.getElementById('loginModule').setAttribute('action', urlApi+'/authLoginUser/');
  // }
}

async function obtainTokenLogin(user, password) {

  // return await fetch(urlApi + "/authLoginUser?user="+user+"&pass="+password).then(response => response.json()).then(data => dataToReturn = data);

  await $.ajax({
    url: urlApi + "/authLoginUser/",
    type: 'POST',
    async: true,
    data: {
      user: user,
      password: password,
    },
    success: function (response) {
      // console.log(response);
      dataToReturn = JSON.parse(response);
    },
    error: function (ex) {
      console.error(ex);
      showAlarms('4', 'nc-icon nc-notification-70', ex.statusCode + " - " + ex.statusText);
    }
  });

  return dataToReturn;
}
