              </select>
            </div> -->

            <div class="col-md-2">
              <label for="">Fecha</label>
              <template>
                <div id="startDate">
                  <date-picker
                    v-model="startDate"
                    valueType="format"
                    format="YYYY-MM-DD"
                  ></date-picker>
                </div>
              </template>
            </div>


            <!-- <div class="col-md-2">
              <label for="">Agrupado por</label>
              <select id="typeChart" class="form-control">
                <option value="hour">Hora</option>
                <option value="day" selected>Dia</option>
                <option value="month">Mensual</option>
              </select>
            </div> -->

            <div class="col-md-2">
              <button
                id="btnTables"
                @click="getDataTabla()"
                class="btn-reportes btn btn-primary"
              >
                Buscar
              </button>
            </div>
          </div>
        </card>

      <div class="row">
        <div class="col-12">
          <card class="strpied-tabled-with-hover"
                body-classes="table-full-width table-responsive"
          >
            <template slot="header">
              <h4 class="card-title">Consumos de diesel sin producción</h4>
              <!-- <p class="card-category">Here is a subtitle for this table</p> -->
            </template>
            <l-table id="tableNoProduction" class="tablesReport table-hover table-striped"
                     :columns="table1.columns"
                     :data="table1.data">
            </l-table>
          </card>

        </div>
      </div>

      <div class="row">
        <div class="col-12">
          <card class="strpied-tabled-with-hover"
                body-classes="table-full-width table-responsive"
          >
            <template slot="header">
              <h4 class="card-title">Consumos de diesel con producción</h4>
              <!-- <p class="card-category">Here is a subtitle for this table</p> -->
            </template>
            <l-table id="tableProduction" class="tablesReport table-hover table-striped"
                     :columns="table2.columns"
                     :data="table2.data">
            </l-table>
          </card>

        </div>
      </div>

      <div class="row">
        <div class="col-12">
          <card class="strpied-tabled-with-hover"
                body-classes="table-full-width table-responsive"
          >
