      <div style="margin-left: -5rem">
        <button class="btnNotification btn-reportes btn btn-primary" @click="notifyVue('top', 'right')" style="display: flex">N</button>
      </div>

    </div>
  </div>
</template>
<script>
  import LTable from 'src/components/Table.vue'
  import Card from 'src/components/Cards/Card.vue'
  import DatePicker from "vue2-datepicker";

  import api, { today } from "./apiData/getApiData";
  // import api, { today } from "./apiData/getApiData_old";

  // const table1_Columns = ['consumo', 'hora', 'operacion'];
  // const table1_Columns = ['operacion', 'consumo', 'hora'];
  const table1_Columns = ['operacion', 'hora', 'consumo', 'distancia'];
  const table2_Columns = ['titulo', 'totales'];

  export default {
    components: {
      LTable,
      Card,
      DatePicker
    },
    data () {
      return {

        startDate: today,

        table1: {
          columns: [...table1_Columns],
          data: null
        },
        table2: {
          columns: [...table1_Columns],
          data: null
        },
        table3: {
          columns: [...table2_Columns],
          data: null
        },

        type: ["", "info", "success", "warning", "danger"],
        notifications: {
          topCenter: false
        }
      }
    },
    methods:{
      notifyVue(verticalAlign, horizontalAlign, color=4, message="Se presento un error, revisar la parte de notificaciones para mas detalles") {
        // const color = Math.floor(Math.random() * 4 + 1);
        // const color = 4;

        this.$notifications.notify({
          message: message,
          icon: "nc-icon nc-app",
          horizontalAlign: horizontalAlign,
          verticalAlign: verticalAlign,
          type: this.type[color]
        });
      },

      async getDataTabla(){

          let dataReturned = await api.getDataTable();

          this.table1.data = [...dataReturned[0].dataTableNoProd];
          this.table2.data = [...dataReturned[0].dataTableSiProd];
          this.table3.data = [...dataReturned[0].consumos];
      }
    },
    mounted(){
      let login = localStorage.getItem("login_token");
      if (!login) {
        this.$router.push("/");
      }

      try {
        document.getElementsByClassName("ifLogin")[0].className = "noLogin";
      } catch (error) {
        console.warn( error )
      }

      api.getLocomotoras();
      this.getDataTabla();
    }
  }
</script>
