<template>
  <div>
    <gmap-map
      id="map"
      :center="center"
      :zoom="zoomMapa"
      :options="options"
      map-type-id="hybrid"
    >
      <gmap-marker :position="center"> </gmap-marker>

      <gmap-polyline v-bind:path.sync='path' v-bind:options="{ strokeColor: '#008000' }"></gmap-polyline>
      <gmap-polyline  id="polylineProd" :key="index+'prod'" v-for="(path, index) in pathsProduccion"  v-bind:path.sync='path' v-bind:options="{ strokeColor: '#F05432' }"></gmap-polyline>

      <gmap-marker
        id="markerMachine"
        :key="index"
        v-for="(m, index) in markers"
        :position="m.position"
        :aria-label="index"
        @click="toggleInfoWindow(m, index)"
        @change="toggleInfoWindow(m, index)"
        icon="http:\\maps.google.com/mapfiles/ms/icons/orange-dot.png"
      >
      </gmap-marker>

      <!-- Info Window para mostrar la informacion basica de las locomotoras -->
      <gmap-info-window
        :options="infoOptions"
        :position="infoWindowPos"
        :opened="infoWinOpen_LessInfo"
        @closeclick="infoWinOpen_LessInfo = true"
      >
        <div v-html="infoContent_LessInfo"></div>
      </gmap-info-window>

    </gmap-map>

    <div id="floating-panel">
      <div class="row">
        <div class="col-md-5">
          <select id="machines" class="form-control">
          </select>
        </div>

        <div class="col-md-4">
          <date-picker
            id="dateTrayectos"
            v-model="startDateTrayecto"
            valueType="format"
            format="YYYY-MM-DD"
            style="margin-top: 0.25rem;"
          ></date-picker>
        </div>

        <div class="col-md-2">
          <button
            id="searchTrayectos"
            class="btn btn-primary"
            @click="obtainDataHistoricos()"
          >
            Buscar
          </button>
        </div>
      </div>
    </div>

    <!-- <div style="margin-left: -5rem">
        <button class="btnNotification btn-reportes btn btn-primary" @click="notifyVue('top', 'right')" style="display: flex">N</button>
    </div> -->

  </div>
</template>

<style>
#floating-panel {
  position: absolute;
  top: 3rem;
  left: 30%;
  z-index: 5;
  background-color: #fff;
  padding: 5px;
  border: 1px solid #999;
  text-align: center;
  font-family: "Roboto", "sans-serif";
  line-height: 30px;
  padding-left: 10px;
  /* display: inline-flex; */
  width: auto !important;
}

#floating-panel .col-md-3 .mx-datepicker {
  display: contents;
}

#searchTrayectos{
  margin-left: 2rem; margin-right: 6rem;
}

#polylineProd{

  z-index: 9999 !important;
}
</style>

<script>
import { API_KEY } from "./Maps/API_KEY";
import Vue from "vue";
import * as VueGoogleMaps from "vue2-google-maps";
import DatePicker from "vue2-datepicker";
import "vue2-datepicker/index.css";

import api, { today } from "./apiData/getApiData";
// import api, { today } from "./apiData/getApiData_old";

Vue.use(VueGoogleMaps, {
  load: {
    key: API_KEY
  }
});

export default {
  components: {
    DatePicker
  },
  data() {
    return {
      startDateTrayecto: today,
      endDate: today,

      firstClick: 1,

      zoomMapa: 6,

      intevalAdvance:null,

      center: { lat: 20.674375, lng: -103.387843 }, // Coordenadas de la Minerva en Guadalajara
      path: null,

      pathsProduccion: null,

      options: {
        styles: [
          {
            featureType: "water",
            stylers: [
              { saturation: 43 },
              { lightness: -11 },
              { hue: "#0088ff" }
            ]
          },
          {
            featureType: "road",
            elementType: "geometry.fill",
            stylers: [
              { hue: "#ff0000" },
              { saturation: -100 },
              { lightness: 99 }
            ]
          },
          {
            featureType: "road",
            elementType: "geometry.stroke",
            stylers: [{ color: "#808080" }, { lightness: 54 }]
          },
          {
            featureType: "landscape.man_made",
            elementType: "geometry.fill",
            stylers: [{ color: "#ece2d9" }]
          },
          {
            featureType: "poi.park",
            elementType: "geometry.fill",
            stylers: [{ color: "#ccdca1" }]
          },
          {
            featureType: "road",
            elementType: "labels.text.fill",
            stylers: [{ color: "#767676" }]
          },
          {
            featureType: "road",
            elementType: "labels.text.stroke",
            stylers: [{ color: "#ffffff" }]
          },
          { featureType: "poi", stylers: [{ visibility: "off" }] },
          {
            featureType: "landscape.natural",
            elementType: "geometry.fill",
            stylers: [{ visibility: "on" }, { color: "#b8cb93" }]
          },
          { featureType: "poi.park", stylers: [{ visibility: "on" }] },
          {
            featureType: "poi.sports_complex",
            stylers: [{ visibility: "on" }]
          },
          { featureType: "poi.medical", stylers: [{ visibility: "on" }] },
          {
            featureType: "poi.business",
            stylers: [{ visibility: "simplified" }]
          }
        ]
      },
      infoContent: "",
      infoContent_LessInfo: "",
      infoWindowPos: {
        lat: 0,
        lng: 0
      },
      infoWinOpen: false,
      infoWinOpen_LessInfo: false,
      currentMidx: null,
      infoOptions: {
        pixelOffset: {
          width: 0,
          height: -35
        }
      },
      markers: null,

      type: ["", "info", "success", "warning", "danger"],
      notifications: {
        topCenter: false
      }
    };
  },
  methods: {
    notifyVue(verticalAlign, horizontalAlign, color=4, message="Se presento un error, revisar la parte de notificaciones para mas detalles") {
      // const color = Math.floor(Math.random() * 4 + 1);
      // const color = 4;

      this.$notifications.notify({
        message: message,
        icon: "nc-icon nc-app",
        horizontalAlign: horizontalAlign,
        verticalAlign: verticalAlign,
        type: this.type[color]
      });
    },

    async obtainDataHistoricos() {

      clearInterval(this.intevalAdvance);
      this.path = [];
      // this.center = {lat:0, lng:0};

      let dataCoordinates = JSON.parse(await api.getDataTrayecto());
      this.zoomMapa = 12;

      this.center = {lat: dataCoordinates[0][0].lat, lng: dataCoordinates[0][0].lng};

      this.pathsProduccion = dataCoordinates[1];

      this.showAvanceTrain();
    },

    showAvanceTrain() {

      this.intevalAdvance = setInterval(() => {

        let imgMarker = document.querySelectorAll( "[src*='/mapfiles/transparent.png']" )[1];

        let temp = JSON.parse(api.showDataTrayecto( this.intevalAdvance ));

        this.path = temp[0];
        this.markers = temp[1];

        // setTimeout(() => {
          imgMarker.parentElement.click();
        // }, 10);

        setTimeout(() => {
          imgMarker.parentElement.click();

          setTimeout(() => {
            imgMarker.parentElement.click();

            setTimeout(() => {
              imgMarker.parentElement.click();
            }, 1499);
          }, 1498);

        }, 1);

      }, 1500);
    },

    deployInfoWindows() {
      this.infoWinOpen_LessInfo = false;
    },

    toggleInfoWindow: function(marker, idx) {

      this.infoWindowPos = marker.position;
      // this.infoContent = this.getInfoWindowContent(marker);
      this.infoContent_LessInfo = this.getInfoWindowContentLessInfo(marker);

      //check if its the same marker that was selected if yes toggle
      if (
        this.currentMidx == idx &&
        !this.infoWinOpen &&
        this.infoWinOpen_LessInfo
      ) {
        this.infoWinOpen = !this.infoWinOpen;
        this.infoWinOpen_LessInfo = !this.infoWinOpen_LessInfo;
      } else if (
        this.currentMidx == idx &&
        this.infoWinOpen &&
        !this.infoWinOpen_LessInfo
      ) {
        this.infoWinOpen = false;
        this.infoWinOpen_LessInfo = true;
      }

      //if different marker set infowindow to open and reset current marker index
      else {
        this.infoWinOpen = false;
        this.infoWinOpen_LessInfo = true;
        this.currentMidx = idx;
      }
    },

    getInfoWindowContentLessInfo: function(marker) {
      return `<div class="">
                      <!--<div>
                        <p class="title is-4">${marker.locomotora}</p>
                        <hr>
                      </div>-->

                    <div class="content">
                      <p style="margin: 4px 0 4px 0" class="cardContent"><b>Velocidad:</b> ${marker.velocidad} </p>
                      <p style="margin: 4px 0 4px 0" class="cardContent"><b>Nvl. Diesel (lts):</b> ${marker.lts_combustible} </p>
                      <p style="margin: 4px 0 4px 0" class="cardContent"><b>Nvl. Diesel (%):</b> ${marker.n_combustible} </p>
                      <p style="margin: 4px 0 4px 0" class="cardContent"><b>Arranque:</b> ${marker.arranque} </p>
                      <p style="margin: 4px 0 4px 0" class="cardContent"><b>Operacion:</b> ${marker.operacion} </p>
                      <p style="margin: 4px 0 4px 0" class="cardContent"><b>Fecha:</b> ${marker.fecha} </p>
                    </div>
                </div>`;
    }
  },
  mounted() {

    let login = localStorage.getItem("login_token");
    if (!login) {
      this.$router.push("/");
    }

    try {
      document.getElementsByClassName("ifLogin")[0].className = "noLogin";
    } catch (error) {
      console.error( error )
    }

    api.getLocomotoras();
  }
};
</script>

<style>
#map {
  min-height: calc(100vh - 123px);
}
.cardContent {
  font-family: "Courier New";
  font-size: 1.062em;
}
</style>
