function proceesDataMachine(dataSended, lastLevelTank, typeChart){
console.log( dataSended );
  let grouperPrevious = null;
  let tempData = dataSended[0].map( (data, index, dataArr) => {

      let sum = 0;
      let count = 0;
      dataArr.filter( dArr => {

        if(dArr.GROUPER == data.GROUPER){

          sum += Number(dArr.STATUS_TANQUE);
          count++;
        }
      });

      return { STATUS_TANQUE: (sum / count), GROUPER: data.GROUPER, operacion: data.operacion, oprc: data.oprc }
  });

  tempData = tempData.filter( data => {

    let dataToReturn = null;
    if( data.GROUPER != grouperPrevious ){
      dataToReturn = { STATUS_TANQUE: data.STATUS_TANQUE, GROUPER: data.GROUPER, operacion: data.operacion, oprc: data.oprc };
    }
    grouperPrevious = data.GROUPER;

    return dataToReturn != null ? dataToReturn : 0;
  });

  console.log( tempData );

  if(typeChart == "month"){

    tempData = tempData.map(data => {

      let temp = dataSended[1].filter(ds => {

        if( data.GROUPER == ds.GROUPER )
          return ds
      });

      return {STATUS_TANQUE: (data.STATUS_TANQUE - temp[0].STATUS_TANQUE), GROUPER: data.GROUPER, operacion: data.operacion, oprc: data.oprc};
    });

  }else{

    tempData = tempData.map( data => {

      let newData = { STATUS_TANQUE: (lastLevelTank - data.STATUS_TANQUE), GROUPER: data.GROUPER, operacion: data.operacion, oprc: data.oprc }
      lastLevelTank = data.STATUS_TANQUE;

      return newData;
    });
  }

  let dataTable = dataSended[0].map( dataT => {

    let newData = [];
    newData = tempData.map(tempD => {

      if( dataT.GROUPER == tempD.GROUPER )
        return { STATUS_TANQUE: tempD.STATUS_TANQUE, GROUPER: dataT.GROUPER, operacion: dataT.operacion, oprc: dataT.oprc };
    });

    newData = newData.filter( data => data != undefined ? data : 0 );

    return newData[0];
  });

  return dataTable;
}

function obtainDistanceTable(latitud, longitud){

  latitud = latitud.split(',');
  longitud = longitud.split(',');

  let totLat = latitud.length;
  // let totLng = longitud.length;

  let totDistance = 0;
  for( let i=1; i<=totLat; i++ ){

    totDistance += calculateDistance(Number(latitud[i-1]), Number(longitud[i-1]), Number(latitud[i]), Number(longitud[i]));
  }

  return Number(totDistance.toFixed(3));
}

function obtainDecenso() {

  setInterval( async () => {

    try {

      let idMachine = $('#machines').val() != undefined ? $('#machines').val() : 1;

      let desensoRapido = await fetch(urlApi+"/obtainDecensoDiesel?startDate="+_today+" "+ getActualHour()+"&idMachine="+idMachine).then(json => json.json()).then( data => data );
      // let desensoRapido = await fetch(urlApi+"/obtainDecensoDiesel?startDate=2022-10-13 10&idMachine=1").then(json => json.json()).then( data => data );

      desensoRapido = desensoRapido.map((data, index, arrayData) => { return { TOT_NIVEL: data.TOT_NIVEL - ( arrayData.length > (index+1) ? arrayData[index+1].TOT_NIVEL : arrayData[index].TOT_NIVEL), fecha: data.fechaPLC, idMaquina: data.idMaquina } } );
      // desensoRapido = desensoRapido.map((data, index, arrayData) => { return { TOT_NIVEL: data.TOT_NIVEL - ( arrayData.length > (index+1) ? arrayData[index+1].TOT_NIVEL : 0), fecha: data.fechaPLC, idMaquina: data.idMaquina } } );

      let isDecended = desensoRapido.filter( data => data.TOT_NIVEL >= 30 ? data : 0 );

      if(isDecended.length > 0){

        let dataError = {
          codigoError: "0000",
          message: "Descenso de diesel abrupto",
          typeError: "diesel",
          dataDB: isDecended[0]
        };

        $.ajax({
          url: urlApi + '/guardarRegistroNotificacion',
          type: 'POST',
          async: false,
          data: { dataError },
          success: function (response) {

            document.getElementsByClassName('btnNotification')[0].click();
          },
          error: function (ex) {
            console.error(ex);
            showAlarms('4', 'nc-icon nc-notification-70', "Ocurrio un error al obtener la información", ex.statusCode + " - " + ex.statusText);
          }
        });
      }
    } catch (error) {
      console.warn(error);
    }
  // }, 600000);
    }, 15000);
}

