function obtainDataMarkets(latitud1, latitud2, longitud1, longitud2, date1, date2, tanque_nivel, tanque_litros, arranque, operacion, fechaPLC) {

  let markers = [];

  let speed = CalculateSpeed(latitud1, latitud2, longitud1, longitud2, date1, date2);
  speed = Math.ceil( speed );

  let data = {
    locomotora: "Locomotora Test 1",
    velocidad: speed + " K/H",
    // kilometraje: "200 000",
    // motor: "Encendido",
    // direccion: "Preguntar que es",
    // engranaje: "4",
    // voltajes: "120 v",
    n_combustible: tanque_nivel,
    lts_combustible: tanque_litros,
    fecha: _today,
    HorasTrabajo: 'NA',
    arranque: arranque == 0 ? '<span style="color: red">OFF</span>' : '<span style="color: green">ON</span>',
    operacion: operacion == 0 ? '<span style="color: red">OFF</span>' : '<span style="color: green">ON</span>',
    // operacion: operacion,
    fecha: fechaPLC,
    position: {
      lat: latitud1,
      lng: longitud1
    },
  }

  markers.push(data);

  return markers;
}

