  components: {
    LTable,
    ChartCard,
    StatsCard,
    DatePicker,
    Card,
    ChartJs
  },
  data() {
    return {

      hours: api.getHours(),
      startDate: today,
      endedDate: today,

      encabezado: null,
      encabezadoAvg: null,
      tipoReporte: null,
