              borderWidth: 3,
              // stepped: true,
              stepped: 'before',
              // stepped: 'after',
              // stepped: 'middle',
              fill: true,
              pointRadius: 0
            }

           /*  {
              type: "bar",
              label: "Arranque",
              data: null,
              borderColor: "rgba(255, 175, 3, 0.6)",
              backgroundColor: "rgba(255, 175, 3, 0.6)",
            },
            {
