              label: "Produccion",
              data: null,
              borderColor: "rgba(121, 236, 5, 0.6)",
              backgroundColor: "rgba(121, 236, 5, 0.6)",
            } */
          ]
        },
        options: {
          responsive: true,
          scales: {
            x: {
              display: true,
              title: {
                display: true,
                text: null,
                color: '#575757',
                font: {
                  family: 'Poppins',
                  size: 16,
                  // weight: 'bold',
                  // lineHeight: 1.2,
                },
                padding: {top: 5, left: 0, right: 0, bottom: 15}
              }
            },
            y: {
              display: true,
              title: {
                display: true,
                text: 'Litros',
                color: '#575757',
                font: {
                  family: 'Poppins',
                  size: 17,
                  // style: 'normal',
                  // lineHeight: 1.2
                },
                padding: {top: 30, left: 20, right: 0, bottom: 0}
              }
            }
          }
        }
      },

      type: ["", "info", "success", "warning", "danger"],
      notifications: {
        topCenter: false
      }
    };
  },
  methods: {

    notifyVue(verticalAlign, horizontalAlign, color=4, message="Se presento un error, revisar la parte de notificaciones para mas detalles") {
      // const color = Math.floor(Math.random() * 4 + 1);
      // const color = 4;

      this.$notifications.notify({
        message: message,
        icon: "nc-icon nc-app",
        horizontalAlign: horizontalAlign,
        verticalAlign: verticalAlign,
        type: this.type[color]
      });
    },

    async obtainDatacharts() {

      let xLegend = "";
      let groupBy = document.getElementById("groupByChart").value;
      let typeChart = document.getElementById("typeChart").value;

      switch (groupBy) {
        case "diesel":
          this.encabezado = "Consumo acumulado de diesel";
          this.encabezadoAvg = "Consumo Promediado de diesel";
          break;

        case "kilometer":
          this.encabezado = "Consumo acumulado de kilometros";
          this.encabezadoAvg = "Consumo Promediado de kilometros";
          break;
      }

      let labelsTest;
      let arrayDataLineChart = [];

      switch (typeChart) {

        case "day":
          this.tipoReporte = "Del Día";
          labelsTest = [
            "0:00",
            "1:00",
            "2:00",
            "3:00",
            "4:00",
            "5:00",
            "6:00",
            "7:00",
            "8:00",
            "9:00",
            "10:00",
            "11:00",
            "12:00",
            "13:00",
            "14:00",
            "15:00",
            "16:00",
            "17:00",
            "18:00",
            "19:00",
            "20:00",
            "21:00",
            "22:00",
            "23:00"
          ];
          arrayDataLineChart.push(await api.obtainDataChartLine());
          xLegend = "Horas";
          break;

        case "month":
          this.tipoReporte = "Mensual";
          labelsTest = api.daysInMonthTest();
          arrayDataLineChart.push(await api.obtainDataChartLine());
          xLegend = "Dias";
          break;
      }

      // Line chart data
      this.lineChart.data.labels = labelsTest;
      this.lineChart.data.datasets[0].data = arrayDataLineChart[0][0];
      this.lineChart.data.datasets[1].data = arrayDataLineChart[0][1];
      this.lineChart.data.datasets[2].data = arrayDataLineChart[0][2];

      this.lineChart.options.scales.x.title.text = xLegend;
      this.lineChart.key = new Date().getSeconds().toString();

      this.showCharts();
    },

    // showCharts(dataLineChart, dataBarChart){
    showCharts(){

      this.saveChartLine.destroy();

      const ctxLine = document.getElementById('lineChart');
      this.saveChartLine = new Chart(ctxLine, this.lineChart);
    },

    showNotifications() {
      this.notifyVue("top", "right", 0);
    }
  },
  mounted() {
    let login = localStorage.getItem("login_token");
    if (!login) {
      this.$router.push("/");
    }

    try {
      document.getElementsByClassName("ifLogin")[0].className = "noLogin";
    } catch (error) {
      console.error( error )
    }

    api.setActualHour();

    const ctxLine = document.getElementById('lineChart').getContext('2d');;
    this.saveChartLine = new Chart(ctxLine, this.lineChart);

    api.getLocomotoras();
    this.obtainDatacharts();
  }
};
