<script>
  import LTable from 'src/components/Table.vue'
  import Card from 'src/components/Cards/Card.vue'

  import api, {today} from './apiData/getApiData';
  // import api, {today} from './apiData/getApiData_old';
  import DatePicker from 'vue2-datepicker';
  import 'vue2-datepicker/index.css';

  const tableColumns = ['id', 'codigo', 'mensaje', 'tipo', 'maquina', 'fecha', 'fecha_plc'];

  export default {
    components: {
      LTable,
      Card,
      DatePicker,
    },
    data () {
      return {
        startDate: today,
        endDate: today,

        table1: {
          columns: [...tableColumns],
          data: null
        },

        type: ["", "info", "success", "warning", "danger"],
        notifications: {
          topCenter: false
        }
      }
    },
    methods:{
      notifyVue(verticalAlign, horizontalAlign, color=4, message="Se presento un error, revisar la parte de notificaciones para mas detalles") {
        // const color = Math.floor(Math.random() * 4 + 1);
        // const color = 4;

        this.$notifications.notify({
          message: message,
          icon: "nc-icon nc-app",
          horizontalAlign: horizontalAlign,
          verticalAlign: verticalAlign,
          type: this.type[color]
        });
      },

      getDataNotf(){

        let dataObtained = api.obtenerNotificaciones();

        this.table1.data = [...dataObtained];
      }
    },

    mounted(){

      let login = localStorage.getItem("login_token");
      if (!login) {
        this.$router.push("/");
      }

      try {
        document.getElementsByClassName("ifLogin")[0].className = "noLogin";
      } catch (error) {
        console.error( error )
      }

      this.getDataNotf();
    }
  }
</script>
